import React, { Component } from 'react'
import { Dropdown, Accordion, Badge, Button } from 'react-bootstrap';
import ProfileImage from '../Main/CommonComponents/ProfileImage/ProfileImage';
import { COPY_TO_CLIPBOARD, IS_IN_IFRAM, REMOVE_COOKIE, TOAST_ERROR, TOAST_LOADING, TOAST_UPDATE } from '../../../utils';
import { Link } from 'react-router-dom';
import { BASE_URL, HOST_URL } from '../../../actions/api';
import { logoutUser } from '../../../actions/loginActions';
import { connect } from 'react-redux';
import { checkCollaborator, checkPermission, switchCollaborator } from '../../../actions/collaboratorActions';
import { USER_AUTHENTICATED_LOGIN, USER_TOKEN } from '../../../reducer/keys';
import withRouter from '../../routes/WithRouter';

class UserDropdown extends Component {
    constructor(props) {
        super(props);
        this.state = {
            logOutSpinner: false,
            isOpen: false,
            loading: false
        };
        this.dropdownRef = React.createRef(); // Reference to the dropdown
    }
    logOutAccount = (isAdmin) => {
        isAdmin ? this.setState({ loading: true }) : this.setState({ logOutSpinner: true })
        logoutUser().then(res => {
            REMOVE_COOKIE('__TagshopLogin', 'tagshop.ai');
            localStorage.clear()
            isAdmin ? window.location.href = `${BASE_URL}/v2/users/userLoginBackAdmin` : window.location.reload()
            //Php_Logout()
        }).catch(error => { })
    }
    onEmailCopy = (id) => event => COPY_TO_CLIPBOARD(id)

    componentDidMount() {
        document.addEventListener('mousedown', this.handleOutsideClick);
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleOutsideClick);
    }

    handleToggle = () => {
        this.setState(prevState => ({ isOpen: !prevState.isOpen }));
    };

    handleOutsideClick = (event) => {
        if (this.dropdownRef.current && !this.dropdownRef.current.contains(event.target)) {
            this.setState({ isOpen: false });
        }
    };

    collaboratorSwitcher = (id, owner) => {

        const { setUserToken, setAuthentication, authorized } = this.props
        const userData = authorized?.userData?.user_data
        if (userData.collaborator.activeAccount != owner) {
            const toastId = TOAST_LOADING()
            switchCollaborator(id).then((response) => {
                localStorage.clear()
                var { responseData } = response.data
                localStorage.setItem('emailId', responseData.user.emailId);
                localStorage.setItem('token', responseData.token)

                setUserToken(responseData.token)
                setAuthentication(responseData)
                //checkCollaborator(responseData, navigate)
                TOAST_UPDATE(toastId, "success", "Account switched successfully")
                window.location.href = `${HOST_URL}home`
            }).catch(ex => {
                TOAST_UPDATE(toastId, "error", "Some error occured, please try again later.")
            })
        }

        else TOAST_ERROR("Already logged in account.")

    }

    render() {
        const { authorized, navigate } = this.props
        const { logOutSpinner, show, loading } = this.state

        const user = authorized?.userData?.user_data.user

        const userData = authorized?.userData?.user_data
        const profileImg = user?.picture
        return (
            <Dropdown className="f-center user_profile_ ms-lg-1 h-100" align="end" show={this.state.isOpen} ref={this.dropdownRef}>
                <span className='h-24px border-start d-none d-lg-flex'></span>
                <Dropdown.Toggle variant="link" id="user_profile_" className="btn author_profile p-0 border-0 text-decoration-none d-block overflow-hidden arrow_disabled ms-2" onClick={this.handleToggle}>
                    <ProfileImage className={'symbol symbol-34 rounded-circle overflow-hidden'}
                        imageSize={34}
                        symbolClass={'symbol-label rounded-circle bg-primary fs-6'}
                        name={user?.name} profileUrl={profileImg} networkId={0} />
                </Dropdown.Toggle>
                <Dropdown.Menu className="p-0" show={show}>
                    <div className="f-center flex-column w-100 p-3 border-bottom">
                        <div className="f-center flex-column w-100">
                            <Link to={"/profile"} className="mb-2 p-1 border-gray-100 border rounded-circle" title="Profile">
                                <ProfileImage className={'symbol symbol-80 rounded-circle flex-shrink-0 overflow-hidden'}
                                    imageSize={80}
                                    symbolClass={'symbol-label fw-bold text-uppercase display-2 bg-primary'}
                                    name={user?.name} profileUrl={profileImg} networkId={0} />
                            </Link>
                            <div className="f-center flex-column w-100">
                                <p className=" mb-0">
                                    <Link to={"/profile"} className="fs-6 fw-bold text-break" title="Profile">{user?.name}</Link>
                                </p>
                                <span className="mb-0 text-gray-700 text-break fs-8 cursor-pointer mb-3" id="headerProfileEmail" onClick={this.onEmailCopy("headerProfileEmail")}>{user?.emailId}</span>
                                <Link to={'/profile'} className='btn btn-secondary rounded-pill w-100' style={{ maxWidth: 230 }} onClick={this.handleToggle} title="Profile">My Profile</Link>
                            </div>
                            {userData?.collaborator && Object.keys(userData.collaborator).length > 0 ? <Accordion className='w-100 mt-3 bg-body' defaultActiveKey={userData?.collaborator.workplaces.length == 1 && userData?.UserPlan.activatedAt == null ? 0 : ''}>
                                <Accordion.Item eventKey={0} className='overflow-hidden' style={{ borderRadius: 30 }}>
                                    <Accordion.Header>{`${userData?.UserPlan.activatedAt != null || userData?.collaborator.workplaces.length > 1 ? 'Switch' : 'Collaborator'} Account`}</Accordion.Header>
                                    <Accordion.Body className='p-3'>
                                        {userData.collaborator.workplaces.map(collab => {
                                            return <Button variant={userData.collaborator.activeAccount == collab.parent ? 'light' : 'light-outline'} size='sm' className="p-2 border-0 d-flex align-items-center w-100 mb-1 rounded-pill" onClick={() => userData?.UserPlan.activatedAt != null || userData?.collaborator.workplaces.length > 1 ? this.collaboratorSwitcher(collab.id, collab.parent) : {}}>
                                                <ProfileImage className={'symbol symbol-34 rounded-circle flex-shrink-0 overflow-hidden me-2'}
                                                    imageSize={34}
                                                    symbolClass={'symbol-label fw-bold text-uppercase fs-6 bg-warning'}
                                                    name={'Shlok Bajoria'} profileUrl={collab.UserAdmin.picture} networkId={0} />
                                                <div className="overflow-hidden w-100 text-start" style={{ maxWidth: 210 }}>
                                                    <p className="mb-0 w-100 fw-bold text-ellipsis fs-9">{collab.UserAdmin.name}</p>
                                                    <div className='f-between'>
                                                        <span className='text-gray-600'>{collab.role == 1 ? "Admin" : "Manager"}</span>
                                                        {userData.collaborator.activeAccount == collab.parent ? <i className='text-gray-500'>Current</i> : null}
                                                    </div>
                                                </div>
                                            </Button>
                                        })}
                                        {userData?.UserPlan.activatedAt != null ? <Button variant={userData.collaborator.activeAccount == 0 ? 'light' : 'light-outline'} size='sm' className="p-2 border-0 d-flex align-items-center w-100 mb-1 rounded-pill" onClick={() => this.collaboratorSwitcher(0, 0)}>
                                            <ProfileImage className={'symbol symbol-34 rounded-circle flex-shrink-0 overflow-hidden me-2'}
                                                imageSize={34}
                                                symbolClass={'symbol-label fw-bold text-uppercase fs-6 bg-primary'}
                                                name={user.name} profileUrl={user.picture} networkId={0} />
                                            <div className="overflow-hidden w-100 text-start" style={{ maxWidth: 210 }}>

                                                <div className='f-between'>
                                                    <p className="mb-0 w-100 fw-bold text-ellipsis fs-9">My Account</p>
                                                    {userData.collaborator.activeAccount == 0 ? <i className='text-gray-500'>Current</i> : null}
                                                </div>
                                            </div>
                                        </Button> : null}
                                    </Accordion.Body>
                                </Accordion.Item>
                            </Accordion> : null}
                        </div>
                    </div>
                    <div className="px-5 mb-3">
                        {JSON.parse(localStorage.getItem('isAdmin')) ?
                            <Dropdown.Item eventKey="1" className={`navi-item px-0 py-2 py-xxl-3 border-bottom dropdown-item rounded-0 ${loading ? 'disabled' : ''}`} onClick={() => this.logOutAccount(true)}>
                                <div className="symbol symbol-36 me-3">
                                    <div className="symbol-label text-warning">
                                        <i className="fa-duotone me-0 fa-solid fa-user-gear"></i>
                                    </div>
                                </div>
                                <div className="navi-text">
                                    <div className={`fs-8 fw-semibold text-warning ${loading ? 'spinner spinner-gray' : ''}`}>Back To Admin</div>
                                </div>
                            </Dropdown.Item>
                            : null}
                        {userData && (userData.collaborator == '' || userData.collaborator?.activeAccount == 0) ? <Link eventKey="2" className="navi-item px-0 py-2 py-xxl-3 border-bottom dropdown-item rounded-0 dropdown-item" to={"/transactions"} onClick={this.handleToggle} title="Account">
                            <div className="symbol symbol-36 me-3">
                                <div className="symbol-label">
                                    <i className="fa-duotone me-0 fa-solid fa-user-gear"></i>
                                </div>
                            </div>
                            <div className="navi-text">
                                <div className="fs-8 fw-semibold mb-0">My Account</div>
                                <div className="text-gray-700 fs-9">Account information and plan details</div>
                            </div>
                        </Link> : null}
                        <button eventKey="3" className={`navi-item px-0 py-2 py-xxl-3 ${!localStorage.getItem('shopifyLogin') ? 'border-bottom' : ''} dropdown-item rounded-0 dropdown-item`} onClick={() => checkPermission('networkManager', 'manageAccount') ? this.setState({ isOpen: false }, () => navigate('/social-account')) : {}}>
                            <div className="symbol symbol-36 me-3">
                                <div className="symbol-label">
                                    <i className="fa-duotone me-0 fa-solid fa-users-gear"></i>
                                </div>
                            </div>
                            <div className="navi-text">
                                <div className="fs-8 fw-semibold mb-0">Social Accounts</div>
                                <div className="text-gray-700 fs-9">Account information and plan details</div>
                            </div>
                        </button>
                        {!IS_IN_IFRAM() ?
                            <Dropdown.Item eventKey="4" onClick={() => this.logOutAccount(false)} className={`navi-item px-0 py-2 py-xxl-3 dropdown-item rounded-0 ${logOutSpinner ? 'disabled' : ''}`}>
                                <div className="symbol symbol-36 me-3">
                                    <div className={`symbol-label ${logOutSpinner ? 'text-gray-500' : ''}`}>
                                        <i className="fa-duotone me-0 fa-solid fa-right-from-bracket"></i>
                                    </div>
                                </div>
                                <div className="navi-text">
                                    <div className={`fs-8 fw-semibold ${logOutSpinner ? 'spinner spinner-gray text-gray-500' : ''}`}>Sign Out</div>
                                </div>
                            </Dropdown.Item> : ''}
                    </div>
                </Dropdown.Menu>
            </Dropdown>
        );
    }
}

const mapDispatchToProps = dispatch => ({
    checkCollaborator: (data, navigate) => dispatch(checkCollaborator(data, navigate)),
    setUserToken: (token) => dispatch({ type: USER_TOKEN, payload: token }),
    setAuthentication: (responseData) => dispatch({ type: USER_AUTHENTICATED_LOGIN, payload: { ...responseData } })
})

export default connect(null, mapDispatchToProps)(withRouter(UserDropdown))